import React from 'react';
import '../../App.css';
import travellers from '../../assets/travellers (2).png'

class Main extends React.Component{

    render(){
        return(
        <main>
            <div className="main-1 container main-page">
              <div className="main-1-desc"><span className="desc-1">
                  Wyrusz w podr z Travelly.
                </span>
                <span className="desc-2">Planuj, tworzc list miejc, ktre chcesz zwiedzi, tak by <span className="span--font-weight">bya to niezapomniana przygoda.</span>
                </span>
                <span className="desc-3">Zapisz wszystkie potrzebne informacje w Travelly.
                </span>
        
              </div>
              <div className="main-1-img"><img src={travellers} alt="travellers with suitcases"/></div>
            </div>
            <div className="main-2">
              <h2>Dowiedz si wicej
              </h2>
              <div className="main-2--background-img">
                <div className="blocks-info-4">
                  <div className="block">
                    <h3>Terminarz</h3>
                    <p>Twrz harmonogram podry, dodajc miejsca, ktre chcesz zwiedzi.
        
                    </p>
                  </div>
                  <div className="block">
                    <h3>Lista rzeczy</h3>
                    <p>
                       Korzystajc z listy, nie zapomnisz o niezbdnych rzeczach, potrzebnych w podrzy.
                    </p>
                  </div>
                  <div className="block">
                    <h3>Trasa</h3>
                    <p>
                      Sprawdzaj jak obra tras od jednego punktu podry, do drugiego.
                    </p>
                  </div>
                  <div className="block">
                    <h3>Notatki</h3>
                    <p>Chcesz zapisa najwaniejsze dla ciebie informacje? Teraz moesz je mie na wycignicie rki.
        
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </main>
        )

    }
}

export default Main